import { useHttp } from "../hooks/http.hook";

const useMoviesServise = () => {
    const apiKey = '94f7bec1b91c0b7133e5fa7be293f498';
    const apiBase = 'https://api.themoviedb.org/';
    const baseImage = 'https://image.tmdb.org/t/p/w500/';
    const {request} = useHttp();

    const getMoviesRecommendations = async () => {
        const response = await request(`${apiBase}3/movie/12/recommendations?api_key=${apiKey}&language=en-US`);
        return transformMovie(response);
    } 

    const getMovieById = async () => {
        const response = await request(`${apiBase}3/movie/12?api_key=${apiKey}&language=en-US`);
        return transformMovie(response);
    }

    const transformMovie = ( movie ) => {
        const {id, title, poster_path, overview: str} = movie;
        console.log(str.slice(0,130))
        return {
            id,
            title,
            overview: str.length > 100 ? str.slice(0, 140) + '....' : str,
            image: baseImage + poster_path,
        }
    }

    return {
        getMoviesRecommendations,
        getMovieById
    };
}

export default useMoviesServise