import {useState, useEffect} from 'react';
import { Link } from "react-router-dom";
import useMoviesServise from "../../services/useMoviesService";

import './randomMovie.scss';

const RandomMovie = () => {
	const [data, setData] = useState({ id: 0, image: '', title: '', overview:''});
	const {getMovieById} = useMoviesServise();

	useEffect(() => {
		getMovieById().then( data => setData(data));
	}, []);

	const {id, title, image, overview} = data;
	return (
		<div className='random'>
		<div className="random__bg" style={{backgroundImage : `url(${image})`}}></div>
			<div className="random__body _container">
				<div className="random__text">
					<div className="random__title">
						{title}
					</div>
					<div className='random__descr'>
						{overview}
					</div>
				</div>	
				<Link to={`/movies/${id}`} className="btn">Read more</Link>
			</div>
		</div>
	)
}

export default RandomMovie;